<template>
	<div id="store-setting">
		<public-header title="商家中心"></public-header>
		<public-footer></public-footer>
		<div class="content">
			<div class="header">
				<div class="logo">
					<img :src="store.logo" alt="">
				</div>
				<div class="title">{{store.title}}</div>
			</div>
			<van-cell-group class="margin-10-b">
				<van-cell is-link :to="util.getUrl({path: '/pages/shop/select'})">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/cut_shop.png" alt="">
						</div>
						<div>切换门店</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-b">
				<van-cell is-link :to="util.getUrl({path: '/pages/shop/index'})">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/shop_info.png" alt="">
						</div>
						<div>门店信息</div>
					</div>
				</van-cell>
				<van-cell v-if="store">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/shop_status.png" alt="">
						</div>
						<div>营业状态</div>
					</div>
					<iswitch :value="store.is_in_business" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="store.is_in_business" type="is_in_business"></iswitch>
				</van-cell>
				<van-cell v-if="store">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/auto_takeOrder.png" alt="">
						</div>
						<div>自动接单</div>
					</div>
					<iswitch :value="store.auto_handel_order" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="store.auto_handel_order" type="auto_handel_order"></iswitch>
				</van-cell>
				<van-cell v-if="store">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/auto_deliveryOrder.png" alt="">
						</div>
						<div>自动通知配送员抢单</div>
					</div>
					<iswitch :value="store.auto_notice_deliveryer" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="store.auto_notice_deliveryer" type="auto_notice_deliveryer"></iswitch>
				</van-cell>
				<van-cell v-if="manager && manager.extra">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/wx_templateNews.png" alt="">
						</div>
						<div>微信模板消息提醒</div>
					</div>
					<iswitch :value="manager.extra.accept_wechat_notice" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="manager.extra.accept_wechat_notice" type="accept_wechat_notice"></iswitch>
				</van-cell>
				<van-cell v-if="manager && manager.extra">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/tel_remind.png" alt="">
						</div>
						<div>语音电话提醒</div>
					</div>
					<iswitch :value="manager.extra.accept_voice_notice" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="manager.extra.accept_voice_notice" type="accept_voice_notice"></iswitch>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/shop/phonic'})">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/auto_deliveryOrder.png" alt="">
						</div>
						<div>语音提醒</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group>
				<van-cell is-link :to="util.getUrl({path: '/pages/more/password'})">
					<div slot="title" class="flex">
						<div class="img">
							<img src="static/img/account_password.png" alt="">
						</div>
						<div>账户密码</div>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="padding-15" @click="onLoginout()">
				<van-button size="normal" class="bg-info font-16">退出</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import {getData} from "@/controller/funcCommon"
	import iswitch from "@/components/switch"
	export default {
		data() {
			return {
				store: {},
				manager: {},
				stat: {},
				notice: 30,
				order_notice_num: 0,
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
			publicFooter,
			iswitch
		},
		methods: {

			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/shop/index/index',
					success: function(res) {
						that.store = res.store;
						that.manager = res.manager;
						that.stat = res.stat;
						that.notice = res.notice;
						that.order_notice_num = res.order_notice_num;
					}
				});
			},

			jsToggleSwitch(data) {
				var that = this;
				this.util.jsToggleSwitch({
					vue: that,
					key: data.keys,
					value: data.value,
					url: 'manage/shop/index/status',
					data: {
						type: data.type,
						value: data.value
					}
				});
			},
			onLoginout() {

			}

		},
		created() {
		},
		mounted() {
			this.onLoad();
			this.util.imap();
			this.util.icloudapi();
		}
	}
</script>

<style>
	#store-setting .content .header{
		background-color:#33aafc;
		text-align:center;
		color:#fff;
		padding:10px 0;
	}
	#store-setting .content .header .logo{
		width:60px;
		height:60px;
		border-radius: 50%;
		border:2px solid #fff;
		margin:0 auto;
		font-size:0;
	}
	#store-setting .content .header .logo img{
		width: 100%;
		height: 100%;
		border-radius:50%;
	}
	#store-setting .content .header .title{
		width:100%;
		box-sizing:border-box;
		padding:0 15px;
		margin-top:10px;
		font-size:15px;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
	#store-setting .content .van-button{
		width: 100%;
	}
	#store-setting .content .iswitch{
		height: 24px;
	}
	#store-setting .content .van-cell .img{
		width: 25px;
		height: 25px;
		margin-right: 10px;
	}
	#store-setting .content .van-cell .img img{
		width: 100%;
		height: 100%;
	}
</style>


